@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap');

html,body,.App,#root {
  width: 100%;
  height: 100%;
  display: inline-block;
}

body {
  font-family: 'Inter', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  background: #f5f5f5;
}
body * {
  box-sizing: border-box;
}
h2 {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  margin-top: 0;
  margin-bottom: 12px;
  text-transform: capitalize;
}
p {
  margin-top: 0;
  margin-bottom: 27px;
}
.text-right {
  text-align: right;
}
.toggle-button * {
  color: #000 !important;
  text-transform: capitalize !important;
}